#app {
  background-color: #303030;
  text-align: center;
  width: 90vw;
  border-radius: 8px;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%)
}

#text-field {
  background-color: #bbbbbb;
  padding: 16px;
  font-family: monospace;
  border-radius: 16px;
  font-size: 16pt;
  color: #303030
}

#text-field-container {
  margin: 40px;
}

.operation {
  color: #b88142
}

.verbose {
  color: #d67474
}

.comp-type {
  color: #aa60eb
}

.infile {
  color: #4c8bd4
}

.infile-2 {
  color: #59f7ad
}

.select {
  background-color: #323232;
  margin: 24px;
  padding: 16px;
}

#options {
  display: grid;
  grid-template-areas: "o o c c" "o o c c" "o o v v" "i i f f";
  grid-gap: 40px;
}

#operation-select {
  grid-area: o;
  border: 1px solid #b88142
}

#verbose-select {
  grid-area: v;
  border: 1px solid #d67474
}

#comp-type-select {
  grid-area: c;
  border: 1px solid #aa60eb
}

#infile-select {
  grid-area: i;
  border: 1px solid #4c8bd4
}

#outfile-select {
  grid-area: f;
  border: 1px solid #59f7ad
}

#verbose-checkbox {
  width: 24px;
  height: 24px;
  border-radius: 8px;
  border: 1px solid grey;
  background-color: white;
  display: inline-block;
}

#verbose-checkbox.v {
  background-color: #d67474
}

h3 {
  margin-top: 0;
  margin-bottom: 16px;
}